@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+KR&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Nanum+Gothic:wght@700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Source+Code+Pro:wght@500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
  background-color: #f7f7f8 !important;
}

header {
  font-family: 'Noto Sans KR', sans-serif !important;
}

main {
  font-family: 'Noto Sans KR', sans-serif !important;
  word-break: break-all;
  display: flex;
  flex-flow: column;
  min-height: calc(100vh - 110px);
}

a {
  cursor: pointer;
}

.dropdown-menu {
  margin: 0.2rem !important;
  min-width: 200px !important;
  border: none !important;
  border-radius: 0.5rem !important;
  box-shadow: 0px 4px 8px 0px rgba(0, 0, 0, 0.2) !important;
}

.dropdown-item {
  height: 40px !important;
  padding: 8px 24px !important;
  font-size: 12pt !important;
  cursor: pointer !important;
}

.dropdown-item:hover {
  background-color: lightgray !important;
}

.dropdown-item:active {
  background-color: gray !important;
}

.dropdown-divider {
  margin: 10px 0 !important;
}
.NavBar .Label {
  display: inline-block;
}

.navbar {
  height: 60px;
  padding: 0;
  font-size: 0.875rem;
  background-color: white;
  box-shadow: 0 0.1px 0.1rem 0.1rem rgba(58, 59, 69, 0.15);
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.navbar-main {
  z-index: 10000;
}

.navbar-brand {
  display: flex;
  height: 100%;
  margin: 0;
  padding: 14px 20px;
  background-color: gray;
  color: white !important;
  cursor: pointer;
}

.nav-icon {
  width: 32px;
  height: 32px;
  margin-right: 10px;
}

.navbar-toggler {
  margin-right: 10px;
  padding: 8px 12px;
  border: none;
  cursor: pointer;
}

.navbar-toggler:hover {
  background-color: gray;
}

.navbar-collapse {
  background-color: white;
}

.nav-item:hover {
  background-color: lightgray;
}

.nav-link {
  width: 100%;
  height: 100%;
  padding: 0 20px !important;
  line-height: 60px;
  cursor: pointer;
}

.dropdown-toggle::after {
  display: none;
}

.nav-bar-area {
  height: 60px;
}
.IconBig {
  margin: auto 0;
  width: 48px;
  height: 48px;
}

.Icon {
  margin: auto 0;
  width: 32px;
  height: 32px;
}

.IconSmall {
  margin: auto 0;
  width: 24px;
  height: 24px;
}

.IconTiny {
  margin: auto 0;
  width: 16px;
  height: 16px;
}

.IconFit {
  vertical-align: unset;
  height: 100%;
  padding: 4px;
}
.ImageLabel {
    vertical-align: middle;
}
.Label {
    display: block;
    margin: auto 0;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    padding: 0 5px;
    font-size: 17px;
}
.FooterArea {
  display: flex;
  height: 50px;
  padding: 15px;
  font-family: "Nanum Gothic", sans-serif !important;
  font-size: 12px;
  background-color: white;
  box-shadow: 0 0.1px 0.1rem 0.1rem rgba(58, 59, 69, 0.15);
}
.HomePage {
    width: 100%;
    height: 100%;
}

.HomePage .ContentArea {
    margin-bottom: 50px;
}

.HomePage .downloadLatestVersion {
    display: flex;
}

@media (max-width: 1000px) {
    .HomePage .downloadLatestVersion .ImageLabel {
        width: 150px !important;
        height: 150px !important;
    }
}

@media (max-width: 600px) {
    .HomePage .downloadLatestVersion .ImageLabel {
        display: none;
    }
}

.HomePage .downloadLatestVersion .ButtonArea {
    margin: 20px 0 0 auto;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
}

.HomePage .downloadLatestVersion .LinkButton {
    padding: 15px 35px;
    font-size: 30px;
}

.HomePage .downloadLatestVersion .downloadButton {
    padding: 15px 35px;
    font-size: 30px;
    color: white;
    background-color: #00cf00;
    text-decoration: none;
}

.HomePage .downloadLatestVersion .downloadButton:hover {
    background-color: #00b400;
}

.HomePage .downloadLatestVersion .downloadButton:active {
    background-color: #008600;
}

.HomePage .features {
    font-size: 15pt;
    font-family: 'Noto Sans KR', sans-serif !important;
}

.HomePage .faq .ContentSubtitle {
    margin-bottom: 10px;
}

.HomePage .faq .ContentTextArea {
    font-family: 'Noto Sans KR', sans-serif !important;
}
.PageFrame {
    margin: 0 auto;
    padding: 10px;
    width: 100%;
    max-width: 1200px;
}
.ContentArea {
    padding: 20px 10px;
    background-color: #ffffff;
    border: 1px solid #e7eaec;
    border-top: 3px solid #e5e5e5;
    border-radius: 5px;
}
.ContentBox {
    margin: 10px;
    padding: 10px;
    background-color: #f3f4f5;
    border: 1px solid #e0e2e4;
    border-radius: 5px;
    font-family: 'Nanum Gothic', sans-serif;
}
.ContentSubtitle {
    margin-bottom: 20px;
    padding: 10px;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    border-bottom: 2px solid #000000;
    font-size: 16pt;
    font-family: 'Nanum Gothic', sans-serif;
}
.ContentTextArea {
    padding: 5px 10px;
    font-size: 12pt;
}

.ContentTextArea p {
    margin-bottom: 16px;
}
.ContentTitleBar {
    margin: 30px 0;
    padding: 30px;
    background-color: #ffffff;
    border: 1px solid #e7eaec;
    border-top: 3px solid #eea3aa;
    border-radius: 5px;
    font-size: 15pt;
}
.ContentSrcViewer {
    margin: 10px;
    padding: 15px;
    color: #f8f8f2;
    background-color: #272822;
    font-family: 'Source Code Pro', monospace;
}
.Button {
	display: block;
    margin: 0 5px;
    padding: 7px 12px;
    background-color: #eff0f2;
    border: none;
    border-radius: 5px;
    font-size: 15px;
	-webkit-user-select: none;
	    -ms-user-select: none;
	        user-select: none;
	outline: none;
	cursor: pointer;
}

.Button:hover {
	background-color: #dadada;
}

.Button:active {
	background-color: #b6b6b6;
}
.LinkButton {
	display: block;
    margin: 0 5px;
    padding: 7px 12px;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    color: #000000;
    background-color: #eff0f2;
    border: none;
    border-radius: 5px;
    font-size: 15px;
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;
}

.LinkButton:hover {
    background-color: #dadada;
}

.LinkButton:active {
	background-color: #b6b6b6;
}
.ButtonArea {
    display: flex;
    padding: 10px;
}

.Versions {
    margin: 30px 0;
}
.DonatePage .downloadInProgress {
    margin: 50px 0;
}

.DonatePage .downloadInProgress .Label {
    margin: auto;
    font-size: 20pt;
}

.DonatePage .donateInfo {
    margin: 20px;
    font-size: 13pt;
}

.DonatePage .ButtonArea {
    display: block;
}

.DonatePage .ButtonArea .LinkButton {
    margin: 10px;
    padding: 25px;
    width: auto;
    font-size: 20pt;
}

.DonatePage .ButtonArea .LinkButton:hover {
    background-color: #ffb120;
}

.DonatePage .thankyou {
    margin: 50px auto;
    font-size: 13pt;
    color: gray;
}
